html{font-size:62.5%}body{font-family:'Lato', Helvetica, Arial, sans-serif;font-size:1.6rem;background-color:#1C2023}button{cursor:pointer}button:disabled{cursor:default}ol,ul{padding:0;margin:0}.btn{border:2px solid #f3f3f3;color:#f3f3f3;padding:5px 20px;text-decoration:none;font-size:1.7rem;transition:0.2s}.btn:hover{background:#f3f3f3;color:black;border-radius:3px}.btn--orange{border:2px solid #faaf14;transition:0.2s}.btn--orange:hover{background:#faaf14;box-shadow:0px 0px 11px #DA9E35;color:black;border-radius:5px}.btn--small{padding:2px 10px;font-size:1.5rem;border:1px solid #f3f3f3}.router-link{color:#DA9E35;transition:all 0.3s;text-decoration:none;font-size:1.8rem}.router-link:hover{color:#ecbf97}.is-active{color:#ff8040}.works-title{color:#B4B6B6;font-weight:200;text-align:center;font-family:Georgia, 'Times New Roman', Times, serif;margin-top:3rem;font-size:4rem}.work-container{display:grid;margin:2rem 5%;width:90%;grid-template-columns:repeat(2, 1fr)}.work-container__work{margin:10% auto;width:95%;height:550px;overflow:hidden;background-size:cover;box-shadow:0px 0px 25px #161616}.work-container .work-about{background:red;heigth:200px}.work-container .work-about h2{font-size:3rem}.work-container .work-darkBackground{height:550px;width:100%;position:relative;bottom:0;z-index:2;backdrop-filter:saturate(150%) blur(10px);background-color:rgba(0,10,10,0.4);transition:all 0.7s;transition-timing-function:cubic-bezier(0.29, 0.56, 0.2, 0.85)}.work-container .work-about{position:relative;bottom:-20px;height:25%;background:#0f1113;padding:5px 0 0 22px;z-index:10;box-shadow:0px -5px 10px -4px #14190f;transition:all 0.4s;transition-timing-function:cubic-bezier(0.32, 0.39, 0.19, 0.99)}.work-container .work-about h2{font-size:1.7rem;color:#f3f3f3}.work-container .work-about .tech-list{display:flex}.work-container .work-about .tech-list__item{color:#faaf14;margin-right:1.5%;list-style:none}.work-container .work-about .links-list{bottom:30px;display:flex;justify-content:flex-end;margin-right:2%;position:relative;list-style-type:none}.work-container .work-about .links-list li{margin-left:1.5%}.work-container .work-about p{color:#f3f3f3;width:60%}.work-container__work:hover .work-about{bottom:25%}.work-container__work:hover .work-darkBackground{bottom:100%}@media only screen and (max-width: 1210px){.work-container__work{background-position:center}}@media only screen and (max-width: 1000px){.work-container{margin:2rem 2%;width:95%}.work-about{height:27%}.work-container__work:hover .work-about{bottom:25%}}@media only screen and (max-width: 1000px){.work-container{grid-template-columns:repeat(1, 1fr);margin:0 auto}.work-container__work{width:90%}}
