html{font-size:62.5%}body{font-family:'Lato', Helvetica, Arial, sans-serif;font-size:1.6rem;background-color:#1C2023}button{cursor:pointer}button:disabled{cursor:default}ol,ul{padding:0;margin:0}.btn{border:2px solid #f3f3f3;color:#f3f3f3;padding:5px 20px;text-decoration:none;font-size:1.7rem;transition:0.2s}.btn:hover{background:#f3f3f3;color:black;border-radius:3px}.btn--orange{border:2px solid #faaf14;transition:0.2s}.btn--orange:hover{background:#faaf14;box-shadow:0px 0px 11px #DA9E35;color:black;border-radius:5px}.btn--small{padding:2px 10px;font-size:1.5rem;border:1px solid #f3f3f3}.router-link{color:#DA9E35;transition:all 0.3s;text-decoration:none;font-size:1.8rem}.router-link:hover{color:#ecbf97}.is-active{color:#ff8040}.welcome{height:100vh;background-image:url(data:image/png;base64,);background-repeat:no-repeat;background-size:cover;border-bottom:1px solid #2d3337}.name{width:25%;margin:1rem auto;text-align:left;color:#f3f3f3;position:absolute;top:20%;left:12%}.name__profession{color:#B4B6B6;font-weight:300;font-size:2.2rem}.name__gabriel{font-size:5rem;margin:0;font-weight:200;color:#DA9E35;font-family:Georgia, 'Times New Roman', Times, serif}.name__gabriel:before{content:"";display:inline-block;width:8px;height:38px;margin-right:10px;background:#DA9E35}.name__line{width:100%;border-bottom:1px solid #c7c9c9}.name__about{font-color:#B4B6B6;font-size:2rem;font-weight:300;letter-spacing:1px;margin-top:12%}.name__social-medias{display:flex;margin-bottom:20%;list-style:none}.name__social-medias li{margin-right:5%}.name__social-medias li a .svg-icon{height:32px}.skills{width:40%;position:absolute;top:60%;left:50%;display:flex;flex-direction:row-reverse}.skills__inner{margin:0 7rem;opacity:0}.skills .skills-1{position:relative;bottom:300px}.skills .skills-2{position:relative;bottom:150px}.skills__title{color:#B4B6B6;font-weight:300;position:relative;right:40%;text-transform:uppercase}.skills__item{color:#f3f3f3;list-style:none;font-weight:600;font-size:2rem;margin-top:18%}.arrow-down{position:absolute;bottom:0%;width:100%;text-align:center}.arrow-down__text{color:#353d42;font-size:2rem}.arrow-down__arrow{color:#B4B6B6;font-size:3rem;font-weight:600;margin:2rem 0;animation:bounce 3s infinite}.anim_left,.anim_right,.anim_top{opacity:0}@keyframes anim_from_left{from{transform:translateX(-120px);opacity:0}to{transform:translateY(0);opacity:1}}@keyframes anim_from_right{from{transform:translateX(120px);opacity:0}to{transform:translateY(0);opacity:1}}@keyframes anim_from_top{from{transform:translateY(-100px);opacity:0}to{transform:translateY(0);opacity:1}}@keyframes bounce{0%{transform:translateY(0px)}20%{transform:translateY(0px)}40%{transform:translateY(0px)}60%{transform:translateY(0px)}75%{transform:translateY(-10px)}80%{transform:translateY(-10px)}100%{transform:translateY(0px)}}@media only screen and (max-width: 1440px){.welcome{background-position:center}.name{top:15%;left:7%}.name__profession{font-size:1.9rem}.name__gariel{font-size:2.5rem}.name__about{font-size:1.8rem}.skills__inner{margin:0 4rem}.skills__item{font-size:1.7rem}}@media only screen and (max-width: 1050px){.skills{top:40%}.skills__inner{margin:0 2rem}.skills .skills-1{bottom:150px}.skills .skills-2{bottom:75px}}@media only screen and (max-width: 768px){.name{top:10%}.name__gabriel{font-size:3rem}.name__gabriel:before{height:24px}.skills{display:grid;grid-template-columns:1fr 1fr;top:10%}.skills__inner{margin:0 2rem}.skills .skills-1{bottom:0px}.skills .skills-2{bottom:0px}}@media only screen and (max-width: 425px){.welcome{height:100vh;display:grid;grid-template-columns:1fr}.name{width:85%;position:static}.name__gabriel{font-size:3.5rem}.name__gabriel:before{font-size:28px}.skills{position:static;grid-template-columns:repeat(3, 1fr)}.skills__title{position:static;font-size:1.7rem}.skills__item{font-size:1.5rem}.arrow-down{display:none}}
