html{font-size:62.5%}body{font-family:'Lato', Helvetica, Arial, sans-serif;font-size:1.6rem;background-color:#1C2023}button{cursor:pointer}button:disabled{cursor:default}ol,ul{padding:0;margin:0}.btn{border:2px solid #f3f3f3;color:#f3f3f3;padding:5px 20px;text-decoration:none;font-size:1.7rem;transition:0.2s}.btn:hover{background:#f3f3f3;color:black;border-radius:3px}.btn--orange{border:2px solid #faaf14;transition:0.2s}.btn--orange:hover{background:#faaf14;box-shadow:0px 0px 11px #DA9E35;color:black;border-radius:5px}.btn--small{padding:2px 10px;font-size:1.5rem;border:1px solid #f3f3f3}.router-link{color:#DA9E35;transition:all 0.3s;text-decoration:none;font-size:1.8rem}.router-link:hover{color:#ecbf97}.is-active{color:#ff8040}
